import {
    ref
} from 'vue';
import getData from './getData.js';
import getCookie from './getCookie.js';

export default function pageContent() {

    let pokemonsArray = ref([]);

    async function getPokemonsArray(page) {
        let offset = page * 10 - 10;
        let pokemons_url = `https://pokeapi.co/api/v2/pokemon/?limit=10&offset=${offset}`;
        let res = await getData(pokemons_url);
        pokemonsArray.value = res.results;
    }


    let currentPage = ref(1);
    if (getCookie('lastPage')) {
        currentPage.value = getCookie('lastPage');
    }

    const MAX_PAGE = ref(10000000);
    (async () => {
        let pokemons_url = `https://pokeapi.co/api/v2/pokemon/?limit=1&offset=0`;
        let res = await getData(pokemons_url)
        MAX_PAGE.value = Math.ceil(res.count / 10);
    })();

    let isLoadingImages = ref(false);
    let pageRange = ref([0, 0, 0, 0, 0, 0, 0, 0, 0]);

    function changePage(num) {
        console.log(num)
        isLoadingImages.value = true;
        setTimeout(() => {
            getPokemonsArray(num);
        }, 1600)
        currentPage.value = num;

        document.cookie = `lastPage=${num}; expires=Tue, 19 Jan 2022 03:14:07 GMT`;

        let arr = [9];
        if (num < 5) {
            for (let i = 0; i < 9; i++)
                arr[i] = i + 1;
        } else if (num > MAX_PAGE.value - 4) {
            for (let i = 0; i < 9; i++) {
                arr[i] = MAX_PAGE.value - 8 + i;
            }
        } else
            for (let i = 0; i < 9; i++)
                arr[i] = num - 4 + i;
        pageRange.value = arr;
        setTimeout(() => {
            isLoadingImages.value = false
        }, 2600);
    }

    changePage(currentPage.value);

    return {
        pokemonsArray,
        isLoadingImages,
        MAX_PAGE,
        currentPage,
        pageRange,
        changePage
    }
}