import {
    reactive
} from 'vue';



export default function showDescription() {
    let description = reactive({
        isShow: false
    });

    function showPokemonDescription(name, url, abilities) {
        description.name = name;
        setTimeout(() => {
            description.isShow = true;
        }, 300);
        description.url = url;
        description.abilities = abilities;
    }
    return {
        showPokemonDescription,
        description
    }
}