import getData from './getData.js';
import {ref, reactive, watch} from 'vue';

export default function pageConetent(url) {

    let iconUrl = ref('');
    let pokemonName = ref('');
    let pokemonAbilities = reactive({});
    let pokemonData = reactive({});
    let showClickAnimation = ref(false);

    function pokemonClickHandler() {
        showClickAnimation.value = true; 
        setTimeout(() => {showClickAnimation.value = false}, 1000);
        console.log(showClickAnimation.value);
    }

    watch(url, newUrl => {
        getPokemonData(newUrl);
    });

    async function getPokemonData(newUrl) {
        pokemonData = await getData(newUrl);
        iconUrl.value = pokemonData.sprites.front_default;
        pokemonName.value = pokemonData.name;
        getPokemonAbilities(pokemonData.abilities);
    }


    async function getPokemonAbilities(abilities) {
        let ability_res = reactive({});
        for(let ability of abilities) {
            ability_res = await getData(ability.ability.url);
            pokemonAbilities[ability_res.name] = ability_res.effect_entries;
            
        }
    }

    getPokemonData(url.value);

    return {
        pokemonName,
        iconUrl,
        pokemonData,
        pokemonAbilities,
        showClickAnimation,
        pokemonClickHandler
    }
}