<template>
  <transition name="first-loading-animation">
    <div class="first-figure" v-if="isLoadingImages"></div>
  </transition>
  <transition name="second-loading-animation">
    <div class="second-figure" v-if="isLoadingImages"></div>
  </transition>
  <transition name="third-loading-animation">
    <div class="third-figure" v-if="isLoadingImages"></div>
  </transition>

  <transition name="show-description">
    <div class="description__root-block" v-if="description.isShow">
      <div class="description__content-block">
        <h2 class="description__name">{{ description.name }}</h2>
        <div class="description__abilities-block">
          <img
            :src="description.url"
            :alt="'Изображение покемона ' + description.name"
            class="description__image"
            :title="'Изображение покемона ' + description.name"
          />
          <template
            v-for="(effects, abName) in description.abilities"
            :key="abName"
          >
            <h3>{{ abName || "no name" }}</h3>
            <p>
              {{ "Effect: " + (effects[0] ? effects[0].effect : "no effects") }}
            </p>
          </template>
        </div>
        <button
          class="description__exit-button"
          @click="description.isShow = false"
        >
          <img
            src="./assets/exit-button.png"
            alt=""
            class="description__exit-img"
          />
        </button>
      </div>
    </div>
  </transition>

  <div class="pokemon__content-block">
    <pokemons-object
      :pokemon-data-url="pokemon.url"
      v-for="pokemon in pokemonsArray"
      :key="pokemon.name"
      class="pokemon"
      @show-description="showPokemonDescription"
    >
    </pokemons-object>
  </div>

  <div class="change-page">
    <button
      class="change-page__button"
      @click="changePage(1)"
      v-show="pageRange[0] > 1"
    >
      &#171;
    </button>
    <button
      v-for="num in pageRange"
      :key="num"
      @click="changePage(num)"
      class="change-page__button"
      :class="{ 'change-page__button_active': num == currentPage }"
    >
      {{ num }}
    </button>
    <button
      class="change-page__button"
      @click="changePage(MAX_PAGE)"
      v-show="pageRange[8] < MAX_PAGE"
    >
      &#187;
    </button>
  </div>
</template>

<script>
import pokemonsObject from "./components/pokemonObject.vue";
import pageContent from "./scripts/pageContent.js";
import showDescription from "./scripts/showDescription.js";

export default {
  name: "App",
  components: {
    pokemonsObject,
  },
  setup() {
    let { showPokemonDescription, description } = showDescription();
    let {
      pokemonsArray,
      isLoadingImages,
      MAX_PAGE,
      currentPage,
      pageRange,
      changePage,
    } = pageContent();

    return {
      pokemonsArray,
      MAX_PAGE,
      currentPage,
      changePage,
      pageRange,
      showPokemonDescription,
      description,
      isLoadingImages,
    };
  },
};
</script>

