<template>
  <img
    :src="iconUrl"
    :alt="pokemonName"
    width="200"
    height="200"
    class="pokemon"
    :class="{ 'click-animation': showClickAnimation }"
    @click="
      $emit('showDescription', pokemonName, iconUrl, pokemonAbilities),
        pokemonClickHandler()
    "
  />
</template>

<script>
import pokemonDataF from "../scripts/pokemonData.js";
import { toRefs } from "vue";

export default {
  name: "pokemon-object",
  props: {
    pokemonDataUrl: {
      type: String,
    },
  },
  emits: ["showDescription"],
  setup(props) {
    let { pokemonDataUrl } = toRefs(props);
    let {
      pokemonName,
      iconUrl,
      pokemonData,
      pokemonAbilities,
      showClickAnimation,
      pokemonClickHandler,
    } = pokemonDataF(pokemonDataUrl);

    return {
      pokemonName,
      iconUrl,
      pokemonData,
      pokemonAbilities,
      showClickAnimation,
      pokemonClickHandler,
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pokemon {
  transition: transform 0.2s linear;
  padding: 20px;
}
.pokemon:hover {
  transform: scale(1.2);
}
.click-animation {
  animation: 1s openDecsription;
}
@keyframes openDecsription {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.4);
  }
  50% {
    transform: scale(0.1);
  }
  100% {
    transform: scale(1);
  }
}
</style>